___CSS_LOADER_EXPORT___.push([module.id, "@media (max-width:600px){.inputs[data-v-48e4f6af]{display:block;height:none}}", "",{"version":3,"sources":["webpack://./index.vue"],"names":[],"mappings":"AA+YA,yBACA,yBACA,aAAA,CACA,WACA,CAGA","sourcesContent":["<template>\r\n    <v-container>\r\n        <v-row class=\"inputs\" style=\"margin-bottom: -30px;;\">\r\n            <v-col sx=\"12\" sm=\"12\" md=\"4\" lg=\"4\" style=\"mb-0 pt-0\" class=\"p-0\" >\r\n                <v-text-field\r\n                    v-model=\"ofi\"\r\n                    label=\"Dependencia\"\r\n                    hide-details\r\n                    outlined\r\n                    clearable\r\n                    dense\r\n                ></v-text-field>\r\n            </v-col>\r\n            <v-col sx=\"12\" sm=\"12\"  md=\"4\" lg=\"4\" style=\"\" class=\"p-0\" >\r\n                <v-text-field\r\n                    v-model=\"dep\"\r\n                    label=\"Oficina\"\r\n                    hide-details\r\n                    outlined\r\n                    clearable\r\n                    dense\r\n                ></v-text-field>\r\n            </v-col>\r\n            <v-col sx=\"6\" xs=\"6\" sm=\"6\" md=\"4\" lg=\"4\" style=\"\" class=\"p-0\" >\r\n                <v-autocomplete              \r\n                    v-model=\"user\"\r\n                    clearable\r\n                    dense\r\n                    label=\"Usuarios\"\r\n                    outlined\r\n                    :items=\"usuarios\"\r\n                    :filter=\"customFilter\"\r\n                    item-value=\"id\"\r\n                    item-text=\"nombres\"\r\n                    :search-input.sync=\"usuarios_search\"\r\n                    required\r\n                >\r\n                    <template v-slot:no-data>\r\n                        <v-list-item>\r\n                            <v-list-item-title>\r\n                                <template>\r\n                                    No hay registros en el inventario\r\n                                </template>\r\n                            </v-list-item-title>\r\n                        </v-list-item>\r\n                    </template>\r\n\r\n                    <template v-slot:item=\"data\">\r\n                        <v-list-item-content>\r\n                            <v-list-item-title v-html=\"data.item.iduoper\">\r\n                            </v-list-item-title>\r\n                            <v-list-item-subtitle>\r\n                                {{ data.item.nombres }}\r\n                            </v-list-item-subtitle>\r\n                        </v-list-item-content>\r\n                    </template>\r\n                </v-autocomplete>\r\n            </v-col>\r\n\r\n        </v-row>\r\n\r\n    <card>\r\n        <v-card-title class=\"pa-0 py-3 pb-5\">\r\n      \r\n            <v-spacer></v-spacer>   \r\n\r\n            <div style=\"display: flex; justify-content:flex-end; align-items:center;\">\r\n\r\n            <div style=\" margin-right:20px; height: 38px;\">\r\n\r\n                <v-menu\r\n                    ref=\"menu\"\r\n                    v-model=\"menu\"\r\n                    offset-y\r\n                    min-width=\"auto\"\r\n                >\r\n                    <template v-slot:activator=\"{ on, attrs }\">\r\n                    <v-text-field\r\n                        v-model=\"date\"\r\n                        label=\"Fecha\"\r\n                        append-icon=\"mdi-calendar\"\r\n                        readonly\r\n                        outlined\r\n                        clearable\r\n                        dense\r\n                        v-bind=\"attrs\"\r\n                        v-on=\"on\"\r\n                    ></v-text-field>\r\n                    </template>\r\n                    <v-date-picker\r\n                    v-model=\"date\"\r\n                    no-title\r\n                    scrollable\r\n                    >\r\n                    <v-spacer></v-spacer>\r\n                    <v-btn\r\n                        text\r\n                        color=\"primary\"\r\n                        @click=\"menu = false\"\r\n                    >\r\n                        Cancel\r\n                    </v-btn>\r\n                    <v-btn\r\n                        text\r\n                        color=\"primary\"\r\n                        @click=\"$refs.menu.save(date)\"\r\n                    >\r\n                        OK\r\n                    </v-btn>\r\n                    </v-date-picker>\r\n                </v-menu>\r\n            </div>\r\n            <div style=\" height: 38px; margin-right: 0px;\">\r\n                <v-text-field\r\n                v-model=\"searchbienes\"\r\n                append-icon=\"mdi-magnify\"\r\n                outlined\r\n                label=\"Buscar\"\r\n                dense\r\n                hide-details\r\n                >\r\n                </v-text-field>\r\n            </div>\r\n        </div>\r\n        </v-card-title>\r\n        <v-data-table\r\n            :headers=\"headBienes\"\r\n            :items=\"bienes\"\r\n            :search=\"searchbienes\"\r\n            :itemsPerPage=\"10\"\r\n            :mobile-breakpoint=\"10\"\r\n            >\r\n            <template v-slot:item.corr_num=\"{ item }\">\r\n                <div>\r\n                    <span>{{item.corr_area }} - {{ item.corr_num }} </span>\r\n                </div>\r\n            </template>\r\n            <template v-slot:item.fecha=\"{ item }\">\r\n                <div>\r\n                    <div>\r\n                        <span>{{item.created_at.substring(0, 10)}}</span>\r\n                    </div>   \r\n                    <span>{{item.created_at.substring(11, 19)}}</span>\r\n                </div>\r\n            </template>  \r\n            <template v-slot:item.acciones=\"{ item }\" >\r\n                <div>\r\n                <v-menu offset-y>\r\n\r\n                        <template\r\n                            v-slot:activator=\"{\r\n                                attrs,\r\n                                on,\r\n                            }\"\r\n                        >\r\n                            <v-btn\r\n                                icon\r\n                                text\r\n                                color=\"primary\"\r\n                                class=\"\"\r\n                                v-bind=\"attrs\"\r\n                                v-on=\"on\"\r\n                            >\r\n                                <v-icon>\r\n                                    mdi-dots-vertical\r\n                                </v-icon>\r\n                            </v-btn>\r\n                        </template>\r\n\r\n                        <v-list dense>\r\n                            <v-subheader\r\n                                >Opciones\r\n                            </v-subheader>\r\n                            <v-list-item-group\r\n                                color=\"primary\">\r\n\r\n                            <v-list-item @click=\"detalle(item)\">\r\n                                <v-list-item-icon  style=\"margin-right: -10px;\" >\r\n                                    <v-icon color=\"primary\" size=\"1.1rem\">mdi-eye</v-icon>\r\n                                </v-list-item-icon>\r\n                                <v-list-item-content>\r\n                                    <span style=\"margin-left: 10px;\" >Ver</span>\r\n                                </v-list-item-content>\r\n                            </v-list-item>\r\n\r\n                            <v-list-item @click=\"eliminar(item)\">\r\n                                <v-list-item-icon  style=\"margin-right: -10px;\" >\r\n                                    <v-icon color=\"primary\" size=\"1.1rem\">mdi-delete</v-icon>\r\n                                </v-list-item-icon>\r\n                                <v-list-item-content>\r\n                                    <span style=\"margin-left: 10px;\" >Eliminar</span>\r\n                                </v-list-item-content>\r\n                            </v-list-item>\r\n                            </v-list-item-group>\r\n\r\n                        </v-list>\r\n                    </v-menu>\r\n                </div>\r\n    \r\n\r\n            </template>\r\n            <template v-slot:item.codigo=\"{ item }\">\r\n                <div class=\"d-flex\">\r\n                    <span class=\"mdi mdi-clipboard-text mr-3\"></span>\r\n                    <span>{{ item.codigo }}</span>\r\n                </div>\r\n            </template>\r\n            <template v-slot:item.responsable=\"{ item }\" style=\"min-width:660px;\">\r\n                <div class=\"d-flex\">\r\n                    <span>{{ item.dni  }}</span>\r\n                </div>\r\n            </template>\r\n\r\n        </v-data-table>\r\n    </card>\r\n\r\n                <v-dialog \r\n                    max-width=\"450\"\r\n                    v-model=\"dialogDetalle\">\r\n                    <v-card>\r\n                    <v-card-title class=\"text-h5 primary\">\r\n                        <span style=\"color: white;\">Detalles </span>\r\n                    </v-card-title>\r\n\r\n                    <v-card-text>\r\n                        <!-- <verDetalleBien :item=\"item\"/> -->\r\n                        <div class=\"d-flex mt-3\" v-if=\"datosDetalle !== null\"  style=\"width:100%; background-color: #fff; align-items: center; justify-content:center;\">\r\n                            <div>\r\n                                <div class=\"\" style=\"font-weight: bold;\">Codigo: </div>\r\n                                <div><span>{{datosDetalle.codigo}}</span></div> \r\n                                <div class=\"\">Oficina:</div>\r\n                                <div> <span style=\"font-weight: bold;\">{{datosDetalle.oficina}}</span></div>\r\n                                <div class=\"\">Dependencia: </div>\r\n                                <div style=\"width: 200px;\"><span>{{datosDetalle.dependencia}}</span></div>\r\n                                <div class=\"\" style=\"font-weight: bold;\">Nombre: </div>\r\n                                <div><span>{{datosDetalle.descripcion}}</span></div>\r\n                            </div>\r\n                            <div>\r\n                                <div class=\"\" style=\"font-weight: bold;\">Responsable: </div>\r\n                                <div><span>DNI: {{datosDetalle.pdni}}</span></div>\r\n                                <div><span>{{datosDetalle.pnombre}} {{datosDetalle.paterno}} {{datosDetalle.materno}}</span></div>\r\n                                <div class=\"\" style=\"font-weight: bold;\">Inventariador:</div>\r\n                                <div> <span>{{datosDetalle.unombre}} {{ datosDetalle.uapellidos}}</span></div>\r\n                                <div class=\"\">-</div>\r\n                                <div> <span></span></div>\r\n                            </div>\r\n\r\n\r\n                        </div>  \r\n                    </v-card-text>\r\n\r\n                    <v-divider></v-divider>\r\n\r\n                    <v-card-actions>\r\n                    <v-spacer></v-spacer>\r\n                    <v-btn\r\n                        color=\"primary\"\r\n                        @click=\"cerrarDetalle()\"\r\n                    >\r\n                        Ok\r\n                    </v-btn>\r\n                    </v-card-actions>\r\n                </v-card>\r\n\r\n\r\n                </v-dialog>\r\n\r\n\r\n    </v-container>\r\n</template>\r\n<script>\r\nimport Layout from \"@/Layouts/AdminLayout\";\r\nexport default {\r\n    metaInfo: { title: \"Dashboard\" },\r\n    layout: Layout,\r\n    data: () => ({\r\n        bienes:[],\r\n        usuarios:[],\r\n        ofi:null,\r\n        dep:null,\r\n        user:null,\r\n        menu:false,\r\n        date: null,\r\n        usuarios_search:\"\",\r\n        searchbienes:\"\",\r\n        buscarBien: \"\",\r\n        date:\"\",\r\n        datosDetalle:null,\r\n        headBienes: [\r\n          { text: 'Codigo', align: 'start', filterable: true, value: 'codigo', width:\"120px\", class:'pl-4 pr-0 grey lighten-1' },\r\n          { text: 'Nombre', align: 'start', filterable: true, value: 'descripcion',width:\"60px\", class:'pl-4 pr-0 grey lighten-1' },\r\n          { text: 'Oficina', align: 'center', filterable: true, value: 'id_area', width:\"70px\", class:'pl-0 pr-0 grey lighten-1' },\r\n          { text: 'Etiqueta', align: 'center', filterable: false, sortable: true, width:\"130px\", value: 'corr_num', class:'pl-0 pr-0 grey lighten-1',},\r\n          { text: 'Fecha', align: 'center', filterable: false, sortable: true, width:\"130px\", value: 'fecha', class:'pl-0 pr-0 grey lighten-1',},\r\n          { align: 'right', value:'acciones', sortable: false, maxWidth:'30px', class:' pl-0 pr-0 grey lighten-1'},\r\n        ],\r\n        dialogDetalle: false,\r\n\r\n    }),\r\n    methods: {\r\n        async getUsuarios(){\r\n            let res = await axios.get(\"/admin/inventario/getUsuarios\");\r\n            this.usuarios = res.data.datos;\r\n            return res.data.datos.data;\r\n        },\r\n\r\n        async getBienes(term = \"\", page = 1) {\r\n            let res = await axios.post(\r\n                \"/admin/inventario/get-bienes-all?page=\" + page,\r\n                { term: term, oficina:this.ofi, dependencia: this.dep, usuario: this.user, fecha: this.date }\r\n            );\r\n            this.bienes = res.data.datos.data;\r\n        },\r\n\r\n        async eliminar(item){\r\n            await axios.delete(`/admin/inventario/eliminarbienadmin/${item.id}`)\r\n            .then(response => {\r\n                this.text = \"Documento eliminado\"\r\n                this.snackbar = true\r\n                this.getBienes()\r\n            }, error => {\r\n                if (error.response.status === 401) {\r\n                    this.dialogError = true;\r\n                    return error;\r\n                }\r\n            });\r\n        },\r\n        customFilter(item, queryText, itemText) {\r\n            const nombres = item.nombres.toLowerCase();\r\n            const apellidos = item.apellidos.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombres.indexOf(searchText) > -1 ||\r\n                apellidos.indexOf(searchText) > -1\r\n             );\r\n        },\r\n        detalle(item){\r\n            this.datosDetalle = item;\r\n            this.dialogDetalle = true;\r\n        },\r\n        cerrarDetalle(){\r\n            this.datosDetalle = null;\r\n            this.dialogDetalle = false;\r\n        }\r\n\r\n        \r\n    },\r\n\r\n    watch:{\r\n\r\n        async ofi(){\r\n            this.getBienes()\r\n        },\r\n\r\n        async dep(){\r\n            this.getBienes()\r\n        },\r\n\r\n        async user(){\r\n            this.getBienes()\r\n        },\r\n        async date(){\r\n            this.getBienes()\r\n        },\r\n        async searchbienes(val) {\r\n            if(val === null){\r\n                this.buscarBien = \"zzz\";\r\n            }\r\n            \r\n            // if (!val) return;\r\n            \r\n\r\n            if(this.ofi !== null){\r\n                let res = await this.getBienes(val);\r\n            }\r\n            else{\r\n                if(this.ofi !== null && this.dep !== null ){\r\n                    let res = await this.getBienes(val);\r\n                }\r\n                else{\r\n                    let res = await this.getBienes(\r\n                        val,\r\n                    );\r\n                }\r\n\r\n            }\r\n            // this.bienes = res;\r\n        },\r\n    },\r\n\r\n    created() {\r\n        this.getBienes();\r\n        this.getUsuarios();\r\n    },\r\n\r\n\r\n};\r\n</script>\r\n<style scoped>\r\n@media (max-width: 600px) {\r\n    .inputs{\r\n        display: block;\r\n        height: none;\r\n    }\r\n    \r\n\r\n}\r\n</style>"],"sourceRoot":""}]);
